import axios from "axios";
import React, { useState } from "react";

export default function Carousel() {
  const [formData, setFormData] = useState({
    Name: "",
    Email: "",
    Phone: "",
    Target: "",
    Program: "",
  });

  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState(null);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  // Updated API endpoint
  const scriptUrl = "https://sheet.best/api/sheets/2cd626b2-0c71-450d-81ea-a9aad6841c17";

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    setError(null);

    // Basic validation
    if (!formData.Name || !formData.Email || !formData.Phone || !formData.Target || !formData.Program) {
      setError("Please fill in all required fields.");
      setIsSubmitting(false);
      return;
    }

    try {
      const response = await fetch(scriptUrl, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });
      if (!response.ok) {
        throw new Error("Network response was not ok");
      }
      alert("Form submitted successfully!");
      setFormData({
        Name: "",
        Email: "",
        Phone: "",
        Target: "",
        Program: "",
      });
    } catch (error) {
      console.error("Error:", error);
      setError("Failed to submit form. Please try again later.");
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div>
      <div className="img-ech">
        <span className="bg-opacity-50"></span>
        <div className="container">
          <h1 className="text-white p-20 text-2xl">NG K NGAY</h1>
        </div>
      </div>
      <div
        className="grid grid-cols-1 md:grid-cols-2 gap-8 pt-6"
        style={{
          width: `${window.innerWidth > 1024 ? "80%" : "100%"}`,
          margin: "0 auto",
        }}
      >
        <div className="p-8 rounded-lg md:col-span-4/6 md:border md:border-gray-300">
          <h1 className="text-3xl font-bold pb-4" style={{ color: "#f57e25" }}>
            LIN H VI CHNG TI
          </h1>
          <ul className="list-disc ml-6 text-xl" style={{ listStyle: "none" }}>
            <li>
              <img
                fetchpriority="high"
                decoding="async"
                width="640"
                height="152"
                src="https://ispace.edu.vn/wp-content/uploads/2020/09/logo-logo-01-1024x243.png"
                className="attachment-large size-large wp-image-8936"
                alt="Logo"
                srcSet="https://ispace.edu.vn/wp-content/uploads/2020/09/logo-logo-01-1024x243.png 1024w, https://ispace.edu.vn/wp-content/uploads/2020/09/logo-logo-01-600x143.png 600w, https://ispace.edu.vn/wp-content/uploads/2020/09/logo-logo-01-e1661395790159.png 300w, https://ispace.edu.vn/wp-content/uploads/2020/09/logo-logo-01-768x183.png 768w, https://ispace.edu.vn/wp-content/uploads/2020/09/logo-logo-01-1536x365.png 1536w, https://ispace.edu.vn/wp-content/uploads/2020/09/logo-logo-01-2048x487.png 2048w"
                sizes="(max-width: 640px) 100vw, 640px"
              />
            </li>
            <li>
              <section className="flex flex-col md:flex-row">
                <div className="w-full md:w-1/2 p-4">
                  <div className="flex items-center">
                    <div className="w-6 h-6 mr-3">
                      <i className="fa fa-phone text-yellow-500 text-xl"></i>
                    </div>
                    <div>
                      <h3 className="text-lg font-semibold">in thoi</h3>
                      <div className="text-base">0938 205 966 (Ms. Chinh)</div>
                    </div>
                  </div>
                </div>
                <div className="w-full md:w-1/2 p-4">
                  <div className="flex items-center">
                    <div className="w-6 h-6 mr-3">
                      <i className="fa fa-envelope text-yellow-500 text-xl"></i>
                    </div>
                    <div>
                      <h3 className="text-lg font-semibold">Email</h3>
                      <div className="text-base">quangcao@ispace.edu.vn</div>
                    </div>
                  </div>
                </div>
              </section>
            </li>
            <li>
              <div className="w-full md:w-full p-4">
                <div className="flex items-center">
                  <div className="w-6 h-6 mr-3">
                    <i className="fa fa-envelope text-yellow-500 text-xl"></i>
                  </div>
                  <div>
                    <h3 className="text-lg font-semibold">a ch</h3>
                    <div className="text-base">
                      240 V Vn Ngn, Phng Bnh TP. Th c, TP. H Ch Minh
                    </div>
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>
        <div className="p-8 rounded-lg md:col-span-2/6 md:border md:border-gray-300">
          <div className="w-full md:w-full px-4 pt-6">
            <div className="bg-gray-800 p-8 rounded-md shadow-md">
              <h3 className="text-white text-2xl font-bold mb-4">
                in thng tin bn di
              </h3>
              {error && (
                <div className="bg-red-500 text-white p-2 mb-4 rounded">
                  {error}
                </div>
              )}
              <form onSubmit={handleSubmit}>
                <input
                  type="text"
                  name="Name"
                  value={formData.Name}
                  onChange={handleChange}
                  className="w-full p-3 rounded-md bg-gray-700 text-white placeholder-gray-400"
                  placeholder="H Tn Bn *"
                  required
                />
                <input
                  type="email"
                  name="Email"
                  value={formData.Email}
                  onChange={handleChange}
                  className="w-full p-3 rounded-md bg-gray-700 text-white placeholder-gray-400 mt-4"
                  placeholder="Email *"
                  required
                />
                <input
                  type="text"
                  name="Phone"
                  value={formData.Phone}
                  onChange={handleChange}
                  className="w-full p-3 rounded-md bg-gray-700 text-white placeholder-gray-400 mt-4"
                  placeholder="S in thoi *"
                  required
                />
                <select
                  name="Target"
                  value={formData.Target}
                  onChange={handleChange}
                  className="w-full p-3 rounded-md bg-gray-700 text-white mt-4"
                  required
                >
                  <option value="">--- i tng ---</option>
                  <option value="HS ang hc lp 12 (ng k gi ch)">
                    HS ang hc lp 12 (ng k gi ch)
                  </option>
                  <option value="HS tt nghip THPT/hon thnh chng trnh lp 12">
                    HS tt nghip THPT/hon thnh chng trnh lp 12
                  </option>
                  <option value="Sinh Vin">Sinh Vin</option>
                  <option value="Ngi i lm">Ngi i lm</option>
                </select>
                <select
                  name="Program"
                  value={formData.Program}
                  className="w-full p-3 rounded-md bg-gray-700 text-white mt-4"
                  onChange={handleChange}
                  required
                >
                  <option value="">--- Chng trnh hc ---</option>
                  <option value="An ninh mng">An ninh mng</option>
                  <option value="Pht trin phn mm">
                    Pht trin phn mm
                  </option>
                  <option value="Thit k  ha">Thit k  ha</option>
                  <option value="Qun tr mng">Qun tr mng</option>
                  <option value="Thng mi in t">Thng mi in t</option>
                </select>
                <button
                  type="submit"
                  className="w-full p-3 rounded-md bg-orange-500 text-white font-bold hover:bg-orange-600 mt-3"
                  disabled={isSubmitting}
                >
                  {isSubmitting ? "ang gi..." : "Gi ngay"}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
